import React from 'react';

export default function CartItem({ item, value }) {
	const { id, title, img, price, total, count } = item;
	const { increment, decrement, remove } = value;

	return (
		<div className="row my-1 text-capitalize text-center">
			<div className="col-10 mx-auto col-lg-2">
				<img
					className="img-fluid "
					alt="shoe product"
					src={img}
					style={{ width: '5rem', height: '4rem' }}
				></img>
			</div>
			<div className="col-10 mx-auto col-lg-2">
				<span className="d-kg-none">Product :</span> {title}
			</div>
			<div className="col-10 mx-auto col-lg-2">
				<span className="d-kg-none">Price : RON</span> {price}
			</div>
			<div className="col-10 mx-auto col-lg-2 my-2 my-lg-0">
				<div className="d-flex justify-content-center">
					<div>
						<span className="btn btn-black mx-1" onClick={() => decrement(id)}>
							-
						</span>
						<span className="btn btn-black mx-1">{count}</span>
						<span className="btn btn-black mx-1" onClick={() => increment(id)}>
							+
						</span>
					</div>
				</div>
			</div>
			<div className="col-10 mx-auto col-lg-2">
				<div className="cart-icon" onClick={() => remove(id)}>
					<i className="fas fa-trash"></i>
				</div>
			</div>
			<div className="col-10 mx-auto col-lg-2">
				<strong> Basket total : RON {total}</strong>
			</div>
		</div>
	);
}
